.result {
  padding: 9rem 0 5rem;
  background-color: #fff; }
  .result h1 {
    margin: 0 0 2rem; }
  .result p {
    margin: 0 0 1.5rem; }
  .result .picked-answers {
    margin: 0 0 3rem; }
    .result .picked-answers .shower {
      color: #2196f3;
      cursor: pointer; }
      .result .picked-answers .shower:hover {
        color: #008fff; }
    .result .picked-answers ul {
      height: 0;
      overflow: hidden;
      -webkit-transition: all .5s ease;
      -o-transition: all .5s ease;
      transition: all .5s ease;
      padding: 0;
      opacity: 0; }
      .result .picked-answers ul.opened {
        height: auto;
        overflow: visible;
        opacity: 1;
        padding: 0 0 3rem; }
      .result .picked-answers ul li {
        list-style: none;
        display: inline-block;
        padding: .2rem .4rem;
        cursor: default;
        background-color: #eee;
        border: .09rem solid #ddd;
        border-radius: .09rem;
        margin: 0 .3rem .25rem 0;
        font-size: .7rem;
        font-weight: 600; }
  .result .result-chart {
    width: 90% !important;
    height: auto !important;
    margin: 0 auto; }
    .result .result-chart svg {
      width: 100% !important;
      height: auto !important;
      position: relative;
      top: -4rem; }
      .result .result-chart svg tspan {
        font-size: .9rem; }

@media (min-width: 0) and (max-width: 768px) {
  .result {
    padding: 8rem 0 5rem; }
    .result h1 {
      font-size: 2rem;
      margin: 0 0 1.75rem; }
    .result p {
      font-size: .9rem; }
    .result .result-chart {
      width: 100% !important; }
      .result .result-chart svg tspan {
        font-size: .75rem; } }



