.answers ul:before {
  content: ' ';
  display: block;
  position: absolute;
  width: 100%;
  height: .1rem;
  border: 0; }

.answers ul:before {
  background: #03001e;
  background: -webkit-linear-gradient(to right, #fb5f94, #ec38bc, #7303c0, #0bb3ff);
  background: -webkit-gradient(linear, left top, right top, from(#fb5f94), color-stop(#ec38bc), color-stop(#7303c0), to(#0bb3ff));
  background: -webkit-linear-gradient(left, #fb5f94, #ec38bc, #7303c0, #0bb3ff);
  background: -o-linear-gradient(left, #fb5f94, #ec38bc, #7303c0, #0bb3ff);
  background: linear-gradient(to right, #fb5f94, #ec38bc, #7303c0, #0bb3ff); }

.answers {
  position: relative; }
  .answers .answer-container {
    height: 8.13rem;
    overflow: hidden;
    margin: 0 0 1rem;
    background-color: #fff;
    -webkit-box-shadow: 0 0 11px 0 rgba(0, 0, 0, 0.08), 0 4px 15px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 0 11px 0 rgba(0, 0, 0, 0.08), 0 4px 15px 0 rgba(0, 0, 0, 0.1); }
  .answers ul {
    margin: 0;
    padding: 0;
    width: 100%;
    display: block;
    border: 0;
    position: relative;
    visibility: visible;
    opacity: 1;
    -webkit-transition: visibility 0s linear 0s, opacity 300ms;
    -o-transition: visibility 0s linear 0s, opacity 300ms;
    transition: visibility 0s linear 0s, opacity 300ms; }
    .answers ul.out {
      visibility: hidden;
      opacity: 0;
      -webkit-transition: visibility 0s linear 300ms, opacity 300ms;
      -o-transition: visibility 0s linear 300ms, opacity 300ms;
      transition: visibility 0s linear 300ms, opacity 300ms; }
    .answers ul li {
      margin: 0;
      width: 100%;
      display: block;
      list-style: none; }
    .answers ul li:last-child .lbl {
      border-color: transparent; }
    .answers ul .lbl {
      display: block;
      width: 100%;
      cursor: pointer;
      -webkit-transition: .9s ease;
      -o-transition: .9s ease;
      transition: .9s ease;
      border-bottom: .1rem solid #f1f1f1; }
      .answers ul .lbl:hover {
        background-color: #f9f9f9; }
      .answers ul .lbl .text {
        white-space: nowrap;
        font-size: 1rem;
        padding: 1.425rem 1.3rem;
        display: inline-block;
        width: 90%; }
      .answers ul .lbl .chk {
        display: none; }
      .answers ul .lbl small {
        color: #aaa;
        margin: 0 0 0 .5rem; }
      .answers ul .lbl .action {
        padding: 1rem 1rem 1rem 0;
        display: inline-block;
        width: 10%;
        font-size: 1.3rem;
        text-align: center; }
  .answers .counter {
    display: block;
    margin: 0;
    padding: 0;
    font-size: .8rem;
    color: #888;
    text-align: right; }
  .answers .btn-container {
    text-align: left;
    display: none; }



